<template>
  <div class="modal fade" id="freightSearchModal" data-backdrop="static" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">
            批量搜索
          </h5>

        </div>
        <div class="modal-body pt-0 pb-0">
          
          <form class="form">
            <div class="form-group">
              <label for="freight_num">菜品编号</label>
              <div class="input-group">
                <input type="text" class="form-control" id="freight_num" placeholder="请输入菜品编号" v-model="freight_num">
                <div class="input-group-append">
                  <span class="input-group-text" id="basic-addon2" @click="clear('num')">清空</span>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label for="freight_named">菜品描述</label>
              <div class="input-group">
                <input type="text" class="form-control" id="freight_num" placeholder="请输入菜品描述" v-model="freight_named">
                <div class="input-group-append">
                  <span class="input-group-text" id="basic-addon2" @click="clear('named')">清空</span>
                </div>
              </div>
            </div>
          </form>

        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" @click="cancel">关闭</button>
          <button type="button" class="btn btn-main" @click="submit">确认搜索</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'FreightViewSearch',
  data() {
    return {
      freight_num: '',
      freight_named: '',
      search_f: false
    }
  },
  methods: {
    validate() {
      this.freight_num = this.freight_num.trim()
      this.freight_named = this.freight_named.trim()
      if ((this.freight_num == '') && (this.freight_named == '')) { return false }

      const res = { }

      if (this.freight_num != '') {
        res.num = this.freight_num
      }
      if (this.freight_named != '') {
        res.named = this.freight_named
      }
      
      return res
    },
    cancel() {
      tool.closeModal('freightSearchModal')
    },
    submitClear() {
      const condition = {
        search_f: 'False'
      }
      if (this.search_f) {
        this.$emit('loadData_Father', null, condition)
      }
      this.search_f = false
    },
    submit(msg = true) {
      const res = this.validate()
      if (res == false) { return false }

      const condition = { 
        search_f: 'True',
        ...res
      }
      this.$emit('loadData_Father', null, condition)
      this.search_f = true
      if (msg) {
        tool.showToast('搜索成功！', 1600)
        tool.closeAllModal()
      }
    },
    clear(typed) {
      if (typed == 'num') {
        this.freight_num = ''
      } else if (typed == 'named') {
        this.freight_named = ''
      }

      this.submitClear()
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


