<template>
  <table class="w-100 pdf-table" :class="`fs-${fs_type}-18`">
    <tbody class="">
      <tr>
        <td width="18%">客户：</td>
        <td colspan="2">{{ the_member.named }}</td>
      </tr>
      <tr>
        <td>地址：</td>
        <td colspan="2">{{ the_member.addr }}</td>
      </tr>
      <tr>
        <td colspan="3">&nbsp;</td>
      </tr>
      <tr>
        <td>联络人：</td>

        <td v-if="!_plus" width="40%">{{ the_member.contact_named }}</td>
        <td v-else width="40%">
          
          <input v-model="member_contact_named" class="pdf-input" placeholder="联系人姓名，可以为空">
          
        </td>

        <td></td>
      </tr>
      <tr>
        <td>电话：</td>

        <td v-if="!_plus">{{ the_member.area.area }}&nbsp;{{ the_member.phoned }}</td>
        <td v-else>
          
          <select class="d-inline-block pdf-input" v-model="member_contact_area">
            <option v-for="a in areas" :key="a.id" :value="a.id">{{ a.area }}</option>
          </select>
          <input v-model="member_contact_phoned" class="pdf-input" placeholder="电话号码，可以为空">
          
        </td>

        <td>传真：</td>
      </tr>
      <tr>
        <td>电邮地址：</td>

        <td v-if="!_plus" colspan="2">{{ the_member.email }}</td>
        <td v-else width="40%">
          
          <input v-model="member_contact_email" class="pdf-input" placeholder="电邮地址，可以为空">
          
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'
import conn from '@/service/conn'

export default {
  name: 'PdfInvoiceMember',
  props: {
    the_member: {
      type: Object
    },
    _plus: {
      type: Boolean,
      default: false
    },
    fs_type: {
      type: String,
      default: 'em'
    }
  },
  data() {
    return {
      member_contact_named: '',
      member_contact_area: 0,
      member_contact_phoned: '',
      member_contact_email: '',
      areas: [

      ]
    }
  },
  mounted() {
    this.member_contact_area = this.the_member.area.id

    conn.area.getList_Area({}, true).then(res => {
      this.areas = res
      if (this.member_contact_area == 0) { this.member_contact_area = res[0].id }
      
    })

    this.member_contact_named = this.the_member.contact_named 
    this.member_contact_phoned = this.the_member.phoned
    this.member_contact_email = this.the_member.email

  },
  methods: {
    _validate() {
      let res = ''
      
      res = validate.val_char(this.member_contact_phoned, '联系人名称')
      if (res != true) { tool.showToast(res); return false }

      res = validate.val_char(this.member_contact_phoned, '电话号码')
      if (res != true) { tool.showToast(res); return false }

      res = validate.val_length(this.member_contact_phoned, '电话号码', 8, 11)
      if (res != true) { tool.showToast(res); return false }

      res = validate.val_email(this.member_contact_email, '电邮地址')
      if (res != true) { tool.showToast(res); return false }


      return true
    },
    collectData() {
      const res = this._validate()
      if (!res) { return false }

      let res_area = 0
      if (this.member_contact_phoned.trim() != '') {
        res_area = this.member_contact_area
      }
      const data = {
        contact_named: this.member_contact_named.trim(),
        phoned: this.member_contact_phoned.trim(),
        area: res_area,
        email: this.member_contact_email.trim()
      }
      
      return data
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

.pdf-input input
  padding: 0px 2px !important
  border: 1px solid !important
  
</style>


