const paymetList = [
  '支票',
  '現金'
]

const unitList = [
  [0, '斤'],
  [1, '包'],
  [2, '件'],
  [3, '扎'],
  [4, '兩'],
  [5, '磅'],
  [6, '條'],
  [7, '張'],
  [8, '盒'],
  [9, '箱'],
  [10, '隻'],
  [11, '排'],
  [12, '枝'],
  [13, '桶'],
  [14, '個'],
  [15, '次'],
  [16, '樽'],
  [17, '罐'],
  [18, '板'],
  [19, '串'],
  [100, '']
]

module.exports = {
  // 
  ser_unit (unit) {
    const res = unitList.filter((e) => {
      if (e[0] == unit) { return true } return false
    })
    return res[0][1]
  },
  //
  ser_time (timed) {
    if ((timed == '') || (timed == '') || (timed == '')) {
      return '-'
    }
    return timed
  },
  //
  ser_prices_status(draft_status, over_status) {
    if (draft_status) {
      return '<div class="draft">草稿</div>'
    }
    if (over_status) {
      return '已结款'
    }
    return '未结款'
  },
  //
  ser_prices_status_pdf(first_pdf, _html_no, _html_yes) {
    if (first_pdf) {
      return _html_no
    }
    return _html_yes
  },
  //
  show_paymet() {
    return paymetList
  },
  ser_listing_paymet(index) {
    return paymetList[index]
  }
}


